import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import {
  HeaderWrapper,
  Logo,
  Nav,
  NavItem,
  NavSearch,
  Addition,
  Button,
  SearchWrapper,
  SearchInfo,
  SearchInfoTitle,
  SearchInfoSwitch,
  SearchInfoItem,
  SearchInfoList
} from './style'
import { CSSTransition } from "react-transition-group";
import { actionCreators } from './store';
import { actionCreators as loginActionCreators } from '../../pages/login/store'


class Header extends Component {

  render() {
    const { list, login, logout } = this.props
    return (
      <HeaderWrapper>
        <Link to="/">
          <Logo />
        </Link>
        <Nav>
          <NavItem className='left active'>首页</NavItem>
          <NavItem className='left'>下载App</NavItem>
          {
            login ? <NavItem
              onClick={logout}
              className='right'>退出</NavItem> :
              <Link to="/login"><NavItem className='right'>登陆</NavItem></Link>
          }

          <NavItem className='right'><span className="iconfont">&#xe636;</span></NavItem>
          <SearchWrapper>
            <CSSTransition
              in={this.props.focused}
              timeout={200}
              classNames="slide"
            >
              <NavSearch
                className={this.props.focused ? 'focused' : ''}
                onFocus={() => this.props.handleInputFocus(list)}
                onBlur={this.props.handleInputBlur}
              ></NavSearch>
            </CSSTransition>
            <span className={this.props.focused ? 'focused iconfont' : 'iconfont'}>&#xe614;</span>
            {this.getListArea()}
          </SearchWrapper>
        </Nav>
        <Addition>
          <Button ClassName='reg'>注册</Button>
          <Button className='writting'><span className="iconfont">&#xe708;</span>写文章</Button>
        </Addition>
      </HeaderWrapper>
    )
  }


  getListArea = () => {
    const { focused, list, page, mouseIn, totalPage, handleMouseEnter, handleChangePage, handleMouseLeave } = this.props;
    const newList = list.toJS();
    const pageList = []

    if (newList.length) {
      for (let i = (page - 1) * 10; i < page * 10; i++) {
        pageList.push(
          <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
        )
      }
    }
    if (focused || mouseIn) {
      return (
        <SearchInfo
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <SearchInfoTitle>
            热门搜索
            <SearchInfoSwitch onClick={() => {
              handleChangePage(page, totalPage)
            }}>
              换一批
            </SearchInfoSwitch>
          </SearchInfoTitle>

          <SearchInfoList>
            {pageList}
          </SearchInfoList>
        </SearchInfo>
      )
    } else {
      return null
    }
  }
}


const mapStateToProps = (state) => {

  let obj = {
    //immutabled的用法
    //get的两种用法相同
    focused: state.get('header').get('focused'),
    list: state.getIn(['header', 'list']),
    page: state.getIn(['header', 'page']),
    mouseIn: state.getIn(['header', 'mouseIn']),
    totalPage: state.getIn(['header', 'totalPage']),
    login: state.getIn(['login', 'login'])
  }
  return obj
}
const mapDispatchToProps = (dispatch) => {
  return {
    handleInputFocus(list) {
      (list.size === 0) && dispatch(actionCreators.getList())
      dispatch(actionCreators.searchFocus())
    },
    handleInputBlur() {
      const action = actionCreators.searchBlur()
      dispatch(action)
    },

    handleMouseEnter() {
      const action = actionCreators.mouseEnter()
      dispatch(action)
    },
    handleMouseLeave() {
      const action = actionCreators.mouseLeave()
      dispatch(action)
    },
    handleChangePage(page, totalPage) {
      console.log(page, totalPage)
      if (page < totalPage) {
        dispatch(actionCreators.changePage(page + 1))
      } else {
        dispatch(actionCreators.changePage(1))
      }
    },
    logout() {
      dispatch(loginActionCreators.logout())
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Header);
