import React, { Component } from 'react';
import { HomeWrapper, HomeLeft, HomeRight } from './style'
import Topic from './component/Topic'
import Writer from './component/Writer'
import Recomments from './component/Recomments'
import List from './component/List';
import { connect } from 'react-redux';
import { actionCreators } from './store'
import { BackTop } from './style'
class Home extends Component {

  handleScroTop = () => {
    window.scrollTo(0, 0);
  }
  render() {
    return (
      <HomeWrapper>
        <HomeLeft>
          <img className='banner-img' src='//upload.jianshu.io/admin_banners/web_images/5006/537c82a858e7b0739b26ccb64d318f5e2ba6fbab.jpeg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540'
            alt="img"
          />
          <Topic />
          <List />
        </HomeLeft>
        <HomeRight>
          <Recomments />
          <Writer />
        </HomeRight>
        {this.props.showScroll ? <BackTop onClick={this.handleScroTop}>回到顶部</BackTop> : null}

      </HomeWrapper>

    );
  }
  componentDidMount() {
    this.props.changeHomeData();
    this.bindEvents();
  }
  bindEvents = () => {
    window.addEventListener('scroll', this.props.changeScrollTopShow)
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.props.changeScrollTopShow)
  }

}



const mapState = (state) => ({
  showScroll: state.getIn(['home', 'showScroll'])
})

const mapDispatch = (dispatch) => ({
  changeHomeData() {
    dispatch(actionCreators.getHomeInfo())
  },
  changeScrollTopShow() {
    if (document.documentElement.scrollTop > 100) {
      dispatch(actionCreators.toggleTopShow(true))
    } else {
      dispatch(actionCreators.toggleTopShow(false))
    }
  }
})

export default connect(mapState, mapDispatch)(Home)
