import styled from 'styled-components';
import logoPic from '../../statics/logo.png'



export const HeaderWrapper = styled.div
`
  position:relative;
  height:56px;
  border-bottom: 1px solid #f0f0f0;
`
export const Logo = styled.div.attrs({
  href:'/'
})
`
  position:absolute;
  top:0;
  left:0;
  height:56px;
  display:block;
  width:100px;
  height:56px;
  background:url(${logoPic});
  background-size:contain;
`


export const Nav = styled.div.attrs()
`
  box-sizing:border-box;
  padding-right:70px;
  width:960px;
  height:56px;
  margin:0 auto;


`
export const NavItem = styled.div.attrs()
`
  line-height:56px;
  padding:0 15px;
  font-size:17px;
  color:#333;
  &.left{
    float:left;
  }
  &.right{
    float:right;
    color:#969696;
  }
  &.active{
    color: #ea6f5a;
  }
`;
export const SearchWrapper = styled.div.attrs()
`
  position:relative;
  float:left;
  .iconfont{
    position:absolute;
    right:5px;
    bottom:5px;
    width:30px;
    line-height:30px;
    border-radius:15px;
    text-align:center;

    &.focused {
      background:#777;
      color:#fff;
    }
  }
`

export const NavSearch = styled.input.attrs({
  placeholder:'搜索'
})
`
  width: 160px;
  height:38px;
  border:none;
  outline:none;
  border-radius:19px;
  background:red;
  margin-top:9px;
  margin-left:20px;
  padding:0 30px 0 20px ;
  box-sizing:border-box;
  background:#eee;
  font-size:14px;
  color:#666;
  &::placeholder{
    color:#999;
  }
  &.focused {
    width:240px;
  }
  &.slide-enter{
    transition: all .2s ease-out;
  }
  &.slide-enter-active{
    width:240px
  }
  &.slide-exit{
    transition: all .2s ease-out;
  }
  &.slide-exit-active{
    width:160px;
  }
`

export const SearchInfo = styled.div.attrs()
`
  position:absolute;
  left:0;
  top:56px;
  width:240px;
  padding:0 20px;
  background:#fff;
  box-shadow:0 0 8px rgba(0,0,0,.2);
`
export const SearchInfoTitle = styled.div.attrs()
`
  margin-top:20px;
  margin-bottom:15px;
  line-height:20px;
  font-size:14px;
  color:#969696;
`
export const SearchInfoSwitch = styled.span.attrs()
`
  float:right;
  font-size:13px;
`

export const SearchInfoItem = styled.a.attrs()
`
display:block;
float:left;
font-size:12px;
margin-right:10px;
margin-bottom: 15px;
padding:0 5px;
line-height:20px;
border:1px solid #ddd;
color:#787878;
border-radius:3px;
`
export const SearchInfoList = styled.div.attrs()
`
overflow:hidden;
`
export const Addition = styled.div.attrs()
`
  position:absolute;
  right:0;
  top:0;
  height:56px;
`
export const Button = styled.div.attrs()
`
  float:right;
  margin-top:9px;
  line-height:38px;
  border-radius:19px;
  border:1px solid #ec6149;
  margin-right:20px;
  padding:0 20px;
  font-size:14px;
  &.reg{
    color:#ec6149;
  }
  &.writting{
    color:#FFF;
    background:#ec6149;
  }
`
