import styled from 'styled-components';
export const DetailWrapper = styled.div.attrs()
  `
  overflow:hidden;
  width:620px;
  margin: 0 auto;
  padding-bottom:100px;
`

export const Header = styled.div.attrs()
  `
  margin:50px 0 20px 0;
  line-height:44px;
  font-size:34px;
  color:#333;
  font-weight:bold;
`
export const Content = styled.div.attrs()
  `
  color:#2f2f2f;
  img {
    width:100%;
  }
  p{
    margin:25px 0;
    font-size:16px;
    line-height:30px;
  }
  b{
    font-weight:bold;
  }
`


