import styled from 'styled-components'

export const HomeWrapper = styled.div.attrs()
  `
  width:960px;
  margin:0 auto;
  overflow:hidden;
`

export const HomeLeft = styled.div.attrs()
  `
  margin-left:15px;
  padding-top:30px;
  width:625px;
  float:left;
  .banner-img{
    width:625px;
    height:270px;
  }
`

export const HomeRight = styled.div.attrs()
  `
  height:100px;
  width:280px;
  float:right;

`

export const TopicWrapper = styled.div.attrs()
  `
  padding:20px 0 10px 0;
  overflow:hidden;
  margin-left:-10px;
  border-bottom:1px solid #dcdcdc;
`

export const TopicItem = styled.div.attrs()
  `
  float:left;
  height:32px;
  line-height:32px;
  margin-left:18px;
  padding-right:10px;
  background:#f7f7f7;
  font-size:14px;
  color:#000;
  border:1px solid #dcdcdc;
  border-radius:4px;
  .topic-pic{
    display:block;
    float:left;
    width:32px;
    height:32px;
    margin-right:10px;
    margin-bottom: 18px;
  }
`
export const ListItem = styled.div.attrs()
  `
  overflow:hidden;
  padding:20px 0;
  border-bottom:1px solid #dcdcdc;
  .list-pic{
    width:125px;
    height:100px;
    display:block;
    float:right;
    border-radius:10px;
  }
`
export const ListInfo = styled.div.attrs()
  `
  width:500px;
  float:left;
  .title {
    font-size:18px;
    line-height:27px;
    font-weight:bold;
    color:#333;
  }
  .desc{
    font-size:13px;
    line-height:24px;
    color:#999;
  }
`

export const RecommentsWrapper = styled.div.attrs()
  `
  margin: 30px 0;
  width:280px;

`
export const RecommentsItem = styled.div.attrs()
  `
  width:280px;
  height:50px;
  margin-bottom:5px;
  background:url(${(props) => props.imgUrl});
  background-size:contain;
`
export const WriterWrapper = styled.div.attrs()
  `
  width:278px;
  border-radius:3px;
  height:200px;
  text-align:center;
`

export const LoadMore = styled.div.attrs()
  `
  width:100%;
  height:40px;
  line-height:40px;
  background:#a5a5a5;
  text-align:center;
  border-radius:20px;
  color:#fff;
  margin:30px 0;
  cursor:pointer;
`

export const BackTop = styled.div.attrs()
  `
position:fixed;
right:100px;
bottom:100px;
width:60px;
height:60px;
line-height:60px;
text-align:center;
border:1px solid #ccc;
font-size:14px;
`

